
.wpte-product-general-style-5{

    .wpte-general-layout-product-img{
        position: relative;
    }

    .wpte-general-layout-product-hover-g-image{
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
        transition: all .4s ease;
    }
    .wpte-general-layout-product-hover-image{
        transition: all .4s ease;
    }

    &:hover .wpte-general-layout-product-hover-image,
    .wpte-general-layout-product-gallery-thumb{
        line-height: normal;
        display: block;
    }

    &:hover{
        .wpte-general-layout-product-hover-g-image{
            opacity: 1;
            visibility: visible;
            transform: scale(1.1);
        }
        .wpte-general-layout-product-hover-image{
            transform: scale(1.1);
        }
    }

    .wpte-general-layout-image-area{
        position: relative;
        overflow: hidden;
    }

    .wpte-general-layout-product-thumb{
        line-height: 0;
    }

    /* Icons */

    .wpte-general-layout-product-icons{
        position: absolute;
        display: flex;
        box-sizing: border-box;
    }

    .wpte-general-layout-icons{
        a, .button{
            display: flex;
            align-items: center;
            justify-content: center;
            box-sizing: border-box;
            font-size: 16px;
            min-width: 40px;
            min-height: 40px;
            background: transparent;
            color: #444;
            border-radius: 50%;
            cursor: pointer;
        }
        a:hover, .button:hover{
            background: transparent;
            color: #fff;
        }
    }

    /* Left */

    .wpte-general-layout-icon-left{
        height: 100%;
        width: auto;
        top: 0;
        flex-direction: column;
        justify-content: start;
        .wpte-general-layout-product-plus{
            height: 46px;
            width: 46px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            cursor: pointer;
        
        }
        .wpte-general-layout-product-cart{
            .button{
                background: transparent;
                width: 100%;
            }
            .button:hover{
                background: transparent;
            }
        }
        .wpte-general-layout-product-plus,
        .wpte-general-layout-product-cart,
        .wpte-general-layout-product-view,
        .wpte-general-layout-product-compare,
        .wpte-general-layout-product-wishlist{
            position: relative;
            z-index: 3;
        }
        .wpte-general-layout-product-cart,
        .wpte-general-layout-product-view,
        .wpte-general-layout-product-compare,
        .wpte-general-layout-product-wishlist{
            opacity: 0;
            visibility: hidden;
            transition: height 350ms ease-in-out, opacity 750ms ease-in-out;
        }
    }

    .wpte-general-layout-product-icons-inner{
        z-index: 3;
        position: absolute;
        left: 15px;
        top: 15px;
        background: #fff;
        width: 46px;
        height: 46px;
        border-radius: 100px;
        transition: all .4s ease;
        .wpte-product-cart-icon-render{
            width: 100%;
        }
    }
    .wpte-general-layout-product-icons-inner:hover{
        height: 214px;
        .wpte-general-layout-product-cart,
        .wpte-general-layout-product-view,
        .wpte-general-layout-product-compare,
        .wpte-general-layout-product-wishlist{
            opacity: 1;
            visibility: visible;
        }
    }
}

